<template>
  <div
    ref="textContainer"
    :style="textStyle"
    class="info-text"
    :class="{'center-text': centerText,
              'border-text': isWrapperBorder,
              'underImage-text': isUnderImage,
              'small-text': totalLines > 15 && !centerText,
              'small-center-text': totalLines > 15 && centerText,
              'single-small-text': InternalID == 8,
              'first-textt': InternalID == 1,
              'eith-textt': InternalID == 99999 }"
  >
    <div ref="textContent" v-html="contentText"></div>
  </div>
</template>

<script>
export default {
  name: 'InfoText',
  props: {
    contentText: String,
    InternalID: Number,
    centerText: Boolean,
    isWrapperBorder: Boolean,
    isUnderImage: Boolean
  },
  data() {
    return {
      totalLines: 0
    }
  },
  mounted() {
    this.calculateTotalLines();
    window.addEventListener('resize', this.calculateTotalLines);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.calculateTotalLines);
  },
  watch: {
    contentText() {
      this.calculateTotalLines();
    }
  },
  computed: {
    textStyle() {
      return this.getTextStyle(this.totalLines);
    }
  },
  methods: {
    calculateTotalLines() {
      setTimeout(() => {
        const textContent = this.$refs.textContent;
        if (textContent) {
          const totalHeight = textContent.clientHeight;
          const lineHeight = parseInt(window.getComputedStyle(textContent).lineHeight, 10);
          const lines = Math.round(totalHeight / lineHeight);
          this.totalLines = lines;
        }
      }, 0);
    },
    getTextStyle(totalLines) {
      // console.log("totalLines: " + totalLines)
      // console.log("isWrapperBorder: " + this.isWrapperBorder)
      // console.log("centerText: " + this.centerText)
      if (this.centerText) {
        switch (true) {
          case totalLines <= 15 && totalLines > 6:
            return {
              width: '70vw',
              fontSize: '22px',
              lineHeight: '26px',
              marginTop: '0vh',
            }
          case totalLines > 15:
            return {
              width: '74vw',
              fontSize: '18px',
              lineHeight: '22px',
              marginTop: '-5vh'
            }
          default:
            return {}
        }
      } else {
        switch (true) {
          case totalLines > 4 && this.isWrapperBorder:
            return {
              fontSize: '20px',
              lineHeight: '24px'
            }
          case totalLines > 14:
            return {
              fontSize: '26px',
              lineHeight: '30px',
              paddingBottom: '7vh'
            }
          case totalLines > 4 && totalLines < 17:
            return {
              paddingBottom: '10vh'
            }
          case totalLines < 17 && this.isWrapperBorder:
            return {
              paddingBottom: '0vh'
            }
          default:
            return {}
        }
      }
    }
  }
}
</script>
  
<style>

.eith-textt{
    font-size: 32px !important;
    line-height: 36px !important;
}
</style>
  